            </el-button>
          </div>
        </div>
      </div>
    </div>

    <!-- Evidence Grid (Masonry Layout) -->
    <div class="evidence-grid-container bg-white rounded-xl shadow-sm border border-gray-200 p-6">
      <div v-if="loading && evidences.length === 0" class="loading-state text-center py-12">
        <Icon icon="mdi:loading" class="text-4xl text-gray-400 animate-spin mb-4" />
        <p class="text-gray-600">Memuat evidence...</p>
