          class="masonry-item"
          :style="getItemStyle(evidence)"
        >
          <div class="evidence-card bg-gray-50 rounded-xl border border-gray-200 overflow-hidden hover:shadow-lg transition-all duration-300 transform hover:-translate-y-1"
               :class="{ 'selected': selectedEvidences.includes(evidence.id) }">
            
            <!-- Selection Checkbox -->
            <div v-if="bulkDeleteMode" class="absolute top-3 left-3 z-10">
              <el-checkbox
                v-model="selectedEvidences"
                :label="evidence.id"
                @change="handleEvidenceSelect"
              />
            </div>

            <!-- Image Preview -->
            <div v-if="isImageFile(evidence.fileName)" class="evidence-image relative">
              <img
                :src="evidence.thumbnailUrl || getImagePlaceholder()"
                :alt="evidence.fileName"
                class="w-full h-48 object-cover"
