            </div>

            <!-- File Icon for Non-Images -->
            <div v-else class="evidence-file-icon bg-gradient-to-br from-blue-500 to-purple-600 h-48 flex items-center justify-center">
              <Icon :icon="getFileIcon(evidence.fileName)" class="text-6xl text-white" />
            </div>

            <!-- Card Content -->
            <div class="p-4">
              <!-- File Name -->
              <h3 class="font-semibold text-gray-900 mb-2 truncate" :title="evidence.fileName">
                {{ evidence.reportId }}
              </h3>

              <!-- Description -->
              <!-- <p v-if="evidence.description" class="text-sm text-gray-600 mb-3 line-clamp-2">
                {{ evidence.description }}
              </p> -->

              <!-- File Info -->
              <div class="file-info space-y-2 mb-4">
                <!-- <div class="flex items-center text-xs text-gray-500">
                  <Icon icon="mdi:file" class="mr-1" />
                  <span>{{ formatFileSize(evidence.fileSize) }}</span>
                </div> -->
                <div class="flex items-center text-xs text-gray-500">
                  <Icon icon="mdi:calendar" class="mr-1" />
                  <span>{{ formatDate(evidence.createdAt) }}</span>
                </div>
                <!-- <div v-if="evidence.reportId" class="flex items-center text-xs text-gray-500">
                  <Icon icon="mdi:link" class="mr-1" />
                  <span>Report #{{ evidence.reportId }}</span>
                </div> -->
              </div>

              <!-- Actions -->
              <div class="actions flex gap-2">
                <el-button
                  size="small"
                  type="primary"
                  @click="viewEvidence(evidence)"
                  icon="el-icon-view"
                >
                  Lihat
                </el-button>
                <!-- <el-button
                  size="small"
                  @click="downloadEvidence(evidence)"
                  icon="el-icon-download"
                >
                  Download
                </el-button> -->
                <!-- <el-button
                  size="small"
