                  @click="confirmDelete(evidence)"
                  icon="el-icon-delete"
                >
                  Hapus
                </el-button> -->
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Load More Button -->
      <div v-if="!pagination.last && evidences.length > 0" class="load-more text-center mt-8">
        <el-button
          @click="loadMore"
