          <!-- <p class="text-gray-600 mb-4">{{ selectedEvidence.fileName }}</p> -->
          <!-- <el-button @click="downloadEvidence(selectedEvidence)" type="primary">
            <Icon icon="mdi:download" class="mr-2" />
            Download File
          </el-button> -->
        </div>
        
        <!-- Evidence Details -->
        <div class="evidence-details mt-6 bg-gray-50 rounded-lg p-4">
          <h4 class="font-semibold text-gray-900 mb-3">Detail Evidence</h4>
          <div class="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
            <div>
              <span class="font-medium text-gray-700">Nama File:</span>
              <span class="text-gray-600 ml-2">{{ selectedEvidence.fileName }}</span>
            </div>
            <div>
              <span class="font-medium text-gray-700">Ukuran:</span>
              <span class="text-gray-600 ml-2">{{ formatFileSize(selectedEvidence.fileSize) }}</span>
            </div>
            <div>
              <span class="font-medium text-gray-700">Diunggah:</span>
              <span class="text-gray-600 ml-2">{{ formatDate(selectedEvidence.createdAt) }}</span>
            </div>
            <div v-if="selectedEvidence.reportId">
              <span class="font-medium text-gray-700">Report ID:</span>
              <span class="text-gray-600 ml-2">#{{ selectedEvidence.reportId }}</span>
            </div>
          </div>
          <div v-if="selectedEvidence.description" class="mt-3">
