            <p class="text-gray-600 mt-1">{{ selectedEvidence.description }}</p>
          </div>
        </div>

        <!-- Admin Actions -->
        <div class="admin-actions mt-4 pt-4 border-t border-gray-200">
          <div class="flex gap-3">
            <el-button @click="downloadEvidence(selectedEvidence)" type="primary">
              <Icon icon="mdi:download" class="mr-2" />
              Download
            </el-button>
            <el-button @click="confirmDelete(selectedEvidence)" type="danger">
              <Icon icon="mdi:delete" class="mr-2" />
              Hapus Evidence
            </el-button>
          </div>
        </div>
