<script>
import { Icon } from "@iconify/vue2";
import evidencesService from '@/services/evidencesService';

export default {
  name: 'ManageEvidence',
  components: {
    Icon
  },
  data() {
    return {
      loading: false,
      loadingMore: false,
      evidences: [],
      pagination: {
        page: 0,
        size: 10,
        totalElements: 0,
        totalPages: 0,
        last: true,
        first: true
      },
      searchQuery: '',
      selectedFileType: '',
      sortBy: 'createdAt',
      sortDir: 'desc',
      
      // Bulk actions
      bulkDeleteMode: false,
      selectedEvidences: [],
      selectAll: false,
      
      // Viewer
      viewerVisible: false,
      selectedEvidence: null,
      
      // Masonry layout
      columns: 3,
      gutter: 24
    }
  },
  computed: {
    imageFiles() {
      if (!Array.isArray(this.evidences)) return 0;
      return this.evidences.filter(evidence => 
        evidence && evidence.fileName && this.isImageFile(evidence.fileName)
      ).length;
    },
    
    documentFiles() {
      if (!Array.isArray(this.evidences)) return 0;
      return this.evidences.filter(evidence => 
        evidence && evidence.fileName && !this.isImageFile(evidence.fileName)
      ).length;
    },

    totalFileSize() {
      if (!Array.isArray(this.evidences)) return 0;
      return this.evidences.reduce((total, evidence) => {
        const fileSize = evidence && typeof evidence.fileSize === 'number' ? evidence.fileSize : 0;
        return total + fileSize;
      }, 0);
    },
    
    masonryStyle() {
      return {
        columnCount: this.columns,
        columnGap: `${this.gutter}px`,
        columnFill: 'balance'
      };
    }
  },
  methods: {
    async fetchEvidences(reset = false) {
      if (reset) {
        this.loading = true;
        this.evidences = [];
        this.pagination.page = 0;
        this.selectedEvidences = [];
        this.selectAll = false;
      } else {
        this.loadingMore = true;
      }

      try {
        const response = await evidencesService.getAllEvidences(
          this.pagination.page,
          this.pagination.size,
          this.sortBy,
          this.sortDir
        );

        if (response.success && response.data.success) {
          const newEvidences = response.data.data.content || [];
          
          // Validate evidence data structure
          const validEvidences = newEvidences.filter(evidence => 
            evidence && typeof evidence === 'object' && evidence.id
          ).map(evidence => ({
            ...evidence,
            fileName: evidence.filename || 'Unknown File',
            fileSize: evidence.sizeFile || 0,
            description: evidence.description || '',
            createdAt: evidence.createdAt || new Date().toISOString()
          }));
          
          if (reset) {
            this.evidences = validEvidences;
          } else {
            this.evidences.push(...validEvidences);
          }
          
          this.pagination = {
            page: response.data.data.page || 0,
            size: response.data.data.size || 10,
            totalElements: response.data.data.totalElements || 0,
            totalPages: response.data.data.totalPages || 0,
            last: response.data.data.last !== false,
            first: response.data.data.first !== false
          };

          // Load thumbnails for images
          this.loadThumbnails(validEvidences);
        }
      } catch (error) {
        console.error('Error fetching evidences:', error);
        this.$message.error('Gagal memuat data evidence');
      } finally {
        this.loading = false;
        this.loadingMore = false;
      }
    },

    async loadThumbnails(evidences) {
      for (const evidence of evidences) {
        if (this.isImageFile(evidence.fileName)) {
          try {
            const response = await evidencesService.viewEvidence(evidence.id);
            if (response.success) {
              this.$set(evidence, 'thumbnailUrl', response.data);
            }
          } catch (error) {
            console.error('Error loading thumbnail:', error);
            // Set fallback placeholder if viewEvidence fails
            this.$set(evidence, 'thumbnailUrl', this.getImagePlaceholder());
          }
        }
      }
    },

    async loadMore() {
      this.pagination.page += 1;
      await this.fetchEvidences(false);
    },

    async refreshEvidences() {
      await this.fetchEvidences(true);
    },

    handleSearch() {
      // Implement search functionality if needed
      this.refreshEvidences();
    },

    handleFilter() {
      this.refreshEvidences();
    },

    handleSort() {
      this.refreshEvidences();
    },

    // Bulk selection methods
    handleSelectAll(value) {
      if (value) {
        this.selectedEvidences = this.evidences.map(evidence => evidence.id);
      } else {
        this.selectedEvidences = [];
      }
    },

    handleEvidenceSelect() {
      this.selectAll = this.selectedEvidences.length === this.evidences.length;
    },

    async confirmBulkDelete() {
      try {
        await this.$confirm(
          `Apakah Anda yakin ingin menghapus ${this.selectedEvidences.length} evidence yang dipilih?`,
          'Konfirmasi Hapus',
          {
            confirmButtonText: 'Ya, Hapus',
            cancelButtonText: 'Batal',
            type: 'warning'
          }
        );

        await this.bulkDeleteEvidences();
      } catch (error) {
        // User cancelled
      }
    },

    async bulkDeleteEvidences() {
      const loading = this.$loading({
        lock: true,
        text: 'Menghapus evidence...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      try {
        const deletePromises = this.selectedEvidences.map(evidenceId =>
          evidencesService.deleteEvidence(evidenceId)
        );

        await Promise.all(deletePromises);
        
        this.$message.success(`Berhasil menghapus ${this.selectedEvidences.length} evidence`);
        this.selectedEvidences = [];
        this.selectAll = false;
        this.bulkDeleteMode = false;
        await this.refreshEvidences();
      } catch (error) {
        console.error('Error deleting evidences:', error);
        this.$message.error('Gagal menghapus beberapa evidence');
      } finally {
        loading.close();
      }
    },

    async confirmDelete(evidence) {
      try {
        await this.$confirm(
          `Apakah Anda yakin ingin menghapus evidence "${evidence.fileName}"?`,
          'Konfirmasi Hapus',
          {
            confirmButtonText: 'Ya, Hapus',
            cancelButtonText: 'Batal',
            type: 'warning'
          }
        );

        await this.deleteEvidence(evidence.id);
      } catch (error) {
        // User cancelled
      }
    },

    async deleteEvidence(evidenceId) {
      const loading = this.$loading({
        lock: true,
        text: 'Menghapus evidence...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      try {
        const response = await evidencesService.deleteEvidence(evidenceId);
        
        if (response.success) {
          this.$message.success('Evidence berhasil dihapus');
          await this.refreshEvidences();
          if (this.viewerVisible && this.selectedEvidence?.id === evidenceId) {
            this.closeViewer();
          }
        } else {
          throw new Error(response.error || 'Gagal menghapus evidence');
        }
      } catch (error) {
        console.error('Error deleting evidence:', error);
        this.$message.error('Gagal menghapus evidence');
      } finally {
        loading.close();
      }
    },

    async viewEvidence(evidence) {
      this.selectedEvidence = evidence;
      
      if (this.isImageFile(evidence.fileName) && !evidence.viewUrl) {
        try {
          const response = await evidencesService.viewEvidence(evidence.id);
          if (response.success) {
            this.$set(evidence, 'viewUrl', response.data);
          }
        } catch (error) {
          console.error('Error loading evidence:', error);
          this.$message.error('Gagal memuat evidence');
          return;
        }
      }
      
      this.viewerVisible = true;
    },

    async downloadEvidence(evidence) {
      try {
        await evidencesService.downloadEvidence(evidence.id, evidence.fileName);
        this.$message.success('File berhasil didownload');
      } catch (error) {
        console.error('Error downloading evidence:', error);
        this.$message.error('Gagal mendownload file');
      }
    },

    closeViewer() {
      this.viewerVisible = false;
      this.selectedEvidence = null;
    },

    isImageFile(fileName) {
      if (!fileName || typeof fileName !== 'string') return false;
      const imageExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.webp'];
      return imageExtensions.some(ext => 
        fileName.toLowerCase().endsWith(ext)
      );
    },

    getFileIcon(fileName) {
      if (!fileName || typeof fileName !== 'string') return 'mdi:file';
      const extension = fileName.toLowerCase().split('.').pop();
      const iconMap = {
        pdf: 'mdi:file-pdf',
        doc: 'mdi:file-word',
        docx: 'mdi:file-word',
        xls: 'mdi:file-excel',
        xlsx: 'mdi:file-excel',
        ppt: 'mdi:file-powerpoint',
        pptx: 'mdi:file-powerpoint',
        txt: 'mdi:file-document-outline',
        zip: 'mdi:zip-box',
        rar: 'mdi:zip-box'
      };
      return iconMap[extension] || 'mdi:file';
    },

    formatFileSize(bytes) {
      if (!bytes || typeof bytes !== 'number') return '0 B';
      const k = 1024;
      const sizes = ['B', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    },

    formatDate(dateString) {
      if (!dateString) return '-';
      const date = new Date(dateString);
      return date.toLocaleDateString('id-ID', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },

    getItemStyle(evidence) {
      return {
        marginBottom: `${this.gutter}px`,
        breakInside: 'avoid',
        display: 'inline-block',
        width: '100%'
      };
    },

    handleImageError(event, evidence) {
      event.target.src = '/api/placeholder/300/200';
    },

    updateColumnCount() {
      const width = window.innerWidth;
      if (width >= 1280) {
        this.columns = 4;
      } else if (width >= 1024) {
        this.columns = 3;
      } else if (width >= 768) {
        this.columns = 2;
      } else {
        this.columns = 1;
      }
    },

    getImagePlaceholder() {
      // SVG placeholder untuk gambar yang gagal dimuat
      const svgContent = `
        <svg width="300" height="200" xmlns="http://www.w3.org/2000/svg">
          <rect width="100%" height="100%" fill="#f0f0f0"/>
          <text x="50%" y="50%" text-anchor="middle" dy=".3em" fill="#999" font-family="Arial, sans-serif" font-size="14">
            Image not available
          </text>
        </svg>
      `;
      return 'data:image/svg+xml;base64,' + btoa(svgContent);
    }
  },

  mounted() {
    this.fetchEvidences(true);
    this.updateColumnCount();
    window.addEventListener('resize', this.updateColumnCount);
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.updateColumnCount);
  }
}
</script>

