import axios from 'axios';

// Base URL for API
// const API_BASE_URL = 'http://localhost:8080/api';
const API_BASE_URL = "https://wbsreborn.lan.go.id/sylow";

// Get token from storage
const getAuthToken = () => {
  return localStorage.getItem('token') || sessionStorage.getItem('token');
};

// Default headers configuration
const getDefaultHeaders = () => {
  const token = getAuthToken();
  const headers = {
    'Content-Type': 'multipart/form-data'
  };
  
  // Add Authorization header only if token exists
  if (token) {
    headers['Authorization'] = `Bearer ${token}`;
  }
  
  return headers;
};

class EvidencesService {
  /**
   * Upload evidence file for a report
   * @param {File} file - The file to upload
   * @param {string} description - Description of the evidence
   * @param {string} reportId - ID of the report
   * @returns {Promise} - API response
   */
  async uploadEvidence(file, description, reportId) {
    try {
      // Check if token exists
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          error: 'Authentication token not found. Please login again.',
          status: 401
        };
      }

      // Create FormData object
      const data = new FormData();
      data.append('file', file);
      data.append('description', description);
      data.append('reportId', reportId);

      // Prepare headers
      const headers = {
        ...getDefaultHeaders()
        // 'Content-Type': 'multipart/form-data'
      };

      // Make API request
      const response = await axios.post(
        `${API_BASE_URL}/evidences/upload`,
        data,
        {
          headers,
          timeout: 30000, // 30 seconds timeout for file upload
          onUploadProgress: (progressEvent) => {
            // Calculate upload progress percentage
            const percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            console.log(`Upload Progress: ${percentCompleted}%`);
          }
        }
      );

      return {
        success: true,
        data: response.data,
        message: 'Evidence uploaded successfully'
      };

    } catch (error) {
      console.error('Error uploading evidence:', error);

      // Handle different types of errors
      if (error.response) {
        // Server responded with error status
        return {
          success: false,
          error: error.response.data?.message || 'Failed to upload evidence',
          status: error.response.status,
          data: error.response.data
        };
      } else if (error.request) {
        // Request was made but no response received
        return {
          success: false,
          error: 'Network error. Please check your connection.',
          status: null
        };
      } else {
        // Something else happened
        return {
          success: false,
          error: error.message || 'Unexpected error occurred',
          status: null
        };
      }
    }
  }

  /**
   * Get evidences for a specific report
   * @param {string} reportId - ID of the report
   * @returns {Promise} - API response
   */
  async getEvidencesByReportId(reportId) {
    try {
      // Check if token exists
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          error: 'Authentication token not found. Please login again.',
          status: 401
        };
      }

      const response = await axios.get(
        `${API_BASE_URL}/evidences/report/${reportId}`,
        {
          headers: getDefaultHeaders(),
          timeout: 10000
        }
      );

      return {
        success: true,
        data: response.data,
        message: 'Evidences retrieved successfully'
      };

    } catch (error) {
      console.error('Error fetching evidences:', error);
      
      return {
        success: false,
        error: error.response?.data?.message || 'Failed to fetch evidences',
        status: error.response?.status
      };
    }
  }

  /**
   * Get evidence list for a specific report
   * @param {string} reportId - ID of the report
   * @returns {Promise} - API response
   */
  async getEvidenceListByReportId(reportId) {
    try {
      // Check if token exists
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          error: 'Authentication token not found. Please login again.',
          status: 401
        };
      }

      const response = await axios.get(
        `${API_BASE_URL}/evidences/report/${reportId}/list`,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          timeout: 10000
        }
      );

      return {
        success: true,
        data: response.data,
        message: 'Evidence list retrieved successfully'
      };

    } catch (error) {
      console.error('Error fetching evidence list:', error);
      
      return {
        success: false,
        error: error.response?.data?.message || 'Failed to fetch evidence list',
        status: error.response?.status
      };
    }
  }

  /**
   * Delete an evidence
   * @param {string} evidenceId - ID of the evidence to delete
   * @returns {Promise} - API response
   */
  async deleteEvidence(evidenceId) {
    try {
      // Check if token exists
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          error: 'Authentication token not found. Please login again.',
          status: 401
        };
      }

      const response = await axios.delete(
        `${API_BASE_URL}/evidences/${evidenceId}`,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          timeout: 10000
        }
      );

      return {
        success: true,
        data: response.data,
        message: 'Evidence deleted successfully'
      };

    } catch (error) {
      console.error('Error deleting evidence:', error);
      
      return {
        success: false,
        error: error.response?.data?.message || 'Failed to delete evidence',
        status: error.response?.status
      };
    }
  }

  /**
   * View evidence file (for images)
   * @param {string} evidenceId - ID of the evidence
   * @returns {Promise} - File blob URL response
   */
  async viewEvidence(evidenceId) {
    try {
      // Check if token exists
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          error: 'Authentication token not found. Please login again.',
          status: 401
        };
      }

      const response = await axios.get(
        `${API_BASE_URL}/evidences/${evidenceId}/view`,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          responseType: 'blob',
          timeout: 30000
        }
      );

      // Create blob URL for viewing
      const imageUrl = window.URL.createObjectURL(new Blob([response.data]));

      return {
        success: true,
        data: imageUrl,
        headers: response.headers,
        message: 'Evidence view URL generated successfully'
      };

    } catch (error) {
      console.error('Error viewing evidence:', error);
      
      return {
        success: false,
        error: error.response?.data?.message || 'Failed to view evidence',
        status: error.response?.status
      };
    }
  }

  /**
   * Download evidence file
   * @param {string} evidenceId - ID of the evidence
   * @param {string} filename - Original filename for download
   * @returns {Promise} - File blob response
   */
  async downloadEvidence(evidenceId, filename) {
    try {
      // Check if token exists
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          error: 'Authentication token not found. Please login again.',
          status: 401
        };
      }

      const response = await axios.get(
        `${API_BASE_URL}/evidences/${evidenceId}/download`,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          responseType: 'blob',
          timeout: 30000
        }
      );

      // Create download link
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', filename || 'evidence-file');
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);

      return {
        success: true,
        data: response.data,
        headers: response.headers,
        message: 'Evidence downloaded successfully'
      };

    } catch (error) {
      console.error('Error downloading evidence:', error);
      
      return {
        success: false,
        error: error.response?.data?.message || 'Failed to download evidence',
        status: error.response?.status
      };
    }
  }

  /**
   * Get all evidences with pagination
   * @param {number} page - Page number (default: 0)
   * @param {number} size - Page size (default: 10) 
   * @param {string} sortBy - Sort field (default: 'createdAt')
   * @param {string} sortDir - Sort direction (default: 'desc')
   * @returns {Promise} - API response
   */
  async getAllEvidences(page = 0, size = 10, sortBy = 'createdAt', sortDir = 'desc') {
    try {
      // Check if token exists
      const token = getAuthToken();
      if (!token) {
        return {
          success: false,
          error: 'Authentication token not found. Please login again.',
          status: 401
        };
      }

      const response = await axios.get(
        `${API_BASE_URL}/evidences`,
        {
          params: { page, size, sortBy, sortDir },
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          timeout: 10000
        }
      );

      return {
        success: true,
        data: response.data,
        message: 'All evidences retrieved successfully'
      };

    } catch (error) {
      console.error('Error fetching all evidences:', error);
      
      return {
        success: false,
        error: error.response?.data?.message || 'Failed to fetch evidences',
        status: error.response?.status
      };
    }
  }
}

// Export a single instance
export default new EvidencesService();